.wadah__beranda {
  row-gap: 7rem;
}

.konten__beranda {
  grid-template-columns: 116px repeat(2, 1fr);
  padding-top: 5.5rem;
  column-gap: 2rem;
  align-items: center;
}

.sosial__beranda {
  display: grid;
  grid-template-columns: max-content;
  row-gap: 1rem;
}

.sosial__beranda-ikon {
  font-size: 1.25rem;
  color: var(--warna-judul);
}

.sosial__beranda-ikon:hover {
  color: var(--warna-judul-gelap);
}

.judul__beranda {
  font-size: var(--ukuran-font-besar);
  margin-bottom: var(--mb-0-25);
}

.tangan__beranda {
  width: 38px;
  height: 38px;
  margin-left: 0.4rem;
}

.subjudul__beranda {
  position: relative;
  font-size: var(--ukuran-h3);
  padding-left: 5.4rem;
  font-weight: var(--font-normal);
  margin-bottom: var(--mb-1);
}

.subjudul__beranda::before {
  content: "";
  position: absolute;
  width: 70px;
  height: 1px;
  background-color: var(--warna-teks);
  left: 0;
  top: 1rem;
}

.deskripsi__beranda {
  max-width: 400px;
  margin-bottom: var(--mb-3);
  text-align: justify;
}

.img__beranda {
  background: url(../../assets/1.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: inset 0 0 0 9px rgba(255 255 255 / 30%);
  order: 1;
  justify-self: center;
  width: 300px;
  height: 300px;
  animation: profil 8s ease-in-out infinite 1s;
}

@keyframes profil {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }

  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }

  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}

.gulir__beranda {
  margin-left: 9.25rem;
}

.roda {
  animation: gulir 2s ease infinite;
}

@keyframes gulir {
  0% {
    transform: translateY(0);
  }

  30% {
    transform: translateY(3.75rem);
  }
}

.nama__gulir-beranda {
  color: var(--warna-judul);
  font-weight: var(--font-medium);
  margin-right: var(--mb-0-25);
  margin-left: 2px;
}

.panah__gulir-beranda {
  font-size: 1.25rem;
  color: var(--warna-judul);
  transition: 0.3s;
}

.gulir__beranda:hover .panah__gulir-beranda {
  transform: translateY(-00.25rem);
  color: var(--warna-judul-gelap);
}

@media screen and (max-width: 992px) {
  .konten__beranda {
    grid-template-columns: 100px repeat(2, 1fr);
    column-gap: 1.25rem;
  }

  .tangan__beranda {
    width: 26px;
    height: 26px;
  }

  .subjudul__beranda {
    padding-left: 3.75rem;
    margin-bottom: var(--mb-1);
  }

  .subjudul__beranda::before {
    width: 42px;
    top: 0.8rem;
  }

  .deskripsi__beranda {
    max-width: initial;
    margin-bottom: var(--mb-2-5);
  }

  .img__beranda {
    width: 250px;
    height: 250px;
    box-shadow: inset 0 0 0 8px rgb(255 255 255 / 30%);
  }

  .gulir__beranda {
    margin-left: 7.5rem;
  }
}

@media screen and (max-width: 768px) {
  .konten__beranda {
    grid-template-columns: 0.5fr 3fr;
    padding-top: 3.5rem;
  }

  .img__beranda {
    order: initial;
    justify-self: initial;
  }

  .data__beranda {
    grid-column: 1 / 3;
  }

  .img__beranda {
    box-shadow: inset 0 0 0 6px rgb(255 255 255 / 30%);
    width: 200px;
    height: 200px;
  }

  .gulir__beranda {
    display: none;
  }
}

@media screen and (max-width: 350px) {
  .img__beranda {
    width: 180px;
    height: 180px;
  }

  .tangan__beranda {
    width: 22px;
    height: 22px;
  }
}
